import React, { Component } from 'react';

import LoversInput from '../components/loversInput';
import ResultView from '../components/resultView';

import { Alert } from 'antd';

class App extends Component {
  state = {
    lovers: {},
    error: null
  }
  eraseValues = () => {
    document.getElementById("firstLover").value = ""
    document.getElementById("secondLover").value = ""
  }
  validateValues = (id) => {
    if (document.getElementById(id).value!=="") {
      return (document.getElementById(id).value)
    } else {
      return null;
    }
  }
  loveInputHandler = () => {
    const lover1 = this.validateValues("firstLover")
    const lover2 = this.validateValues("secondLover")
    if (lover1!==null && lover2!==null){
      const love = Math.floor(Math.random() * 110) + "%"
      const lovers = {firstLover: lover1, secondLover: lover2, loveIndex: love}
      this.setState({lovers: lovers})
      this.eraseValues()
      this.setState({error: null})
    }else{
      const error = (<Alert
        message="Error"
        description="Please Input two names above!"
        type="error"
        showIcon
        style={{width: '90%', margin: 'auto', marginTop: '20pt'}}/>)
      this.setState({error: error})
    }
  }
  render() {
    return (
      <div className="App" style={{marginTop:'20pt'}}>
        <h1>Find Your True Love!</h1>
        <LoversInput  loveInputHandler={this.loveInputHandler}/>
        {this.state.error}
        <ResultView  lovers={this.state.lovers}/>
      </div>
    );
  }
}

export default App;



